@use "global" as *;

.l-u-concept {
	padding-top: 100px;
	padding-bottom: 143px;
}

.p-u-concept {
	////scada/////
	&__scada {
	}
	&__scada-img {
		width: 900px;
		max-width: 90%;
		margin: 0 auto;
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		gap: 60px;
		margin-top: 65px;
		margin-bottom: 113px;
		position: relative;

		@include mq(sm) {
			margin-bottom: 40px;

			&::before {
				content: "";
				display: block;
				padding-top: 64%;
			}

			@include mq(sm) {
				display: block;
			}

			img {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}

	&__img-sp {
		@include mq(sm) {
			width: 80%;
			margin: 34px auto 0;
			position: relative;

			&::before {
				content: "";
				display: block;
				padding-top: 64%;
			}

			img {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}

	&__scada-illustrated {
		text-align: center;

		h3 {
			color: $white;
			background-color: $blue;
			display: inline-block;
			margin-top: 62px;
			margin-bottom: 30px;
			padding: 15px 45px;
			font-size: 17px;
			font-weight: $bold;
			position: relative;

			&::after{
				position: absolute;
				content: '';
				top: 100%;
				left: 50%;
				border: 15px solid transparent;
				border-top: 15px solid #3084BD;
				width: 0;
				height: 0;
			}

			@include mq(sm) {
				margin-top: 52px;
				margin-bottom: 16px;
			}
		}

		p {
			font-size: 18px;
			margin-bottom: 62px;

			span {
				font-size: 17px;
				font-weight: $bold;
				color: $white;
				background-color: $blue;
				display: inline-block;
				padding: 4px 13px;
				letter-spacing: 0.1em;
				border-radius: 7px;
				margin-right: 10px;
			}
		}
		ul {
			display: grid;
			grid-template-columns: 1fr 1fr 1fr;
			gap: 34px;
			margin-bottom: 110px;

			@include mq(sm) {
				display: block;
				width: 80%;
				margin: 0 auto;
			}

			li {
				@include mq(sm) {
					&:not(:first-child) {
						margin-top: 28px;
					}
				}

				img {
				}
			}
		}
	}

	////iot/////
	&__iot {
		margin-top: 150px;
	}
	&__iot-wrap {
		display: flex;
		align-items: center;
		margin-top: 70px;
		margin: 0 calc(50% - 50vw);
		width: 100vw;
		overflow-x: hidden;
		padding-left: 166px;
		position: relative;
		z-index:50;

		@include mq(sm) {
			flex-direction: column;
			padding-left: $padding-sp;
			padding-right: $padding-sp;
		}
	}

	&__iot-img {
		flex: 0 0 55%;
		margin: 0 calc(50% - 50vw);
		// margin-top: 50px;
		margin-left: 96px;

		@include mq(sm) {
			margin-top: 50px;
			flex: 0 0 0;
		}
		img {
		}
	}

	////tolesabi/////
	&__tolesabi {
	}
	&__tolesabi-wrap {
		display: flex;
		flex-direction: row-reverse;
		align-items: center;
		margin-top: 118px;
		margin: 0 calc(50% - 50vw);
		width: 100vw;
		overflow-x: hidden;
		padding-right: 166px;
		position: relative;
		z-index: 10;

		@include mq(sm) {
			flex-direction: column;
			padding-left: $padding-sp;
			padding-right: $padding-sp;
			margin-top: 76px;
		}
	}
	&__tolesabi-img {
		margin-right: 67px;
		flex: 0 0 55%;
		margin: 0 calc(50% - 50vw);

		@include mq(sm) {
			margin-top: 50px;
			flex: 0 0 0px;
		}

		img {
		}
	}

	//////automation////
	&__automation {
		margin-bottom: -250px;
	}
	&__automation-wrap {
		display: flex;
		align-items: center;
		margin-top: 118px;
		margin: 0 calc(50% - 50vw);
		width: 100vw;
		overflow-x: hidden;
		padding-left: 166px;
		position: relative;
		z-index: 10;

		@include mq(sm) {
			flex-direction: column;
			padding-left: $padding-sp;
			padding-right: $padding-sp;
			margin-top: 76px;
		}

		// // z-index
		// // 
		// &::before {
		// 	position: absolute;
		// 	content: "";
		// 	width: 100%;
		// 	height: 25%;
		// 	background-color: #a7cbe3;
		// 	bottom: 0;
		// 	left: 0;
		// 	z-index: -1;
		// }
	}
	&__automation-img {
		margin-left: 65px;
		flex: 0 0 55%;
		margin: 0 calc(50% - 50vw);
		// position: relative;

		// &::before {
		// 	content: "";
		// 	display: block;
		// 	padding-top: 50%;
		// }
	
		// img {
		// 	position: absolute;
		// 	top: 0;
		// 	left: 0;
		// 	width: 100%;
		// 	height: 100%;
		// 	object-fit: cover;
		// }
		

		@include mq(sm){
			margin-top: 50px;
			flex: 0 0 0px;
		}

		img {
		}
	}
}
