@use "global" as *;
////modal////
.grid--modal {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
	gap: 45px 60px;
	width: 950px;
	max-width: 100%;
	margin: 0 auto;
	padding: 165px 0 118px;
	margin-top: -100px;
	position: relative;
	padding-left: $padding-sp;
	padding-right: $padding-sp;
	// z-index: 100;
	justify-items: center;

	@include mq() {
		grid-template-columns: 1fr 1fr;
		gap: 20px;
		padding: 150px 80px 60px;
		// padding-top: 50px;
	}
	@include mq() {
		
		padding: 150px 5px 60px;
		// padding-top: 50px;
	}
}

.p-item__modal--long {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
	gap: 25px;
	width: 1080px;
	max-width: 90%;
	margin: 0 auto;
	padding: 165px 0 118px;
	margin-top: -100px;
	position: relative;
	justify-items: center;
	// z-index:2;

	@include mq() {
		padding: 230px 0 118px;
	}
	@include mq(sm) {
		padding: 200px 0 118px;
	}
}
.p-item__modal--automation {
	display: grid;
