.pt-140 {
	padding-top: 140px;
}

.p-item__modal-inner {
	background-color: #a7cbe3;
	margin: 0 calc(50% - 50vw);
	width: 100vw;

	@include mq() {
		padding-left: 70px;
		padding-right: 70px;
	}

	@include mq(sm) {
		margin-top: 0;
	}
	@include mq(ssm) {
		margin-top: 0;
		padding-left: 50px;
		padding-right: 50px;
	}
	@include mq(se) {
		padding-left: 30px;
		padding-right: 30px;
	}
}
.p-item__modal-inner--green {
	background-color: #97D3E2;
	margin: 0 calc(50% - 50vw);
	width: 100vw;
	position: relative;
	padding: 0 30px;
	// z-index: 1;

	@include mq() {
		margin-top: -200px;
	}
	@include mq(sm) {
		margin-top: -120px;
	}
}
