@use "global" as *;

.c-pagenation{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
    z-index: 10;
    

    li{
        &:not(:last-child){
            margin-right: 10px;
        }
        span{
            display: block;
            padding: 6px 9px;
            color: $white;
            line-height: 1;
            font-size: 12px;
            border: 1px solid #1693B8;
            background-color: #1693B8;

        }

        a{
            display: block;
            padding: 6px 9px;
            color: $black;
            line-height: 1;
            border: 1px solid $black;



        }
    }
}