@use "sass:math";

////////////////////
// 
////////////////////
// 
$l-inner: 1100px;
// 

// 
$padding-pc: 25px;
$padding-sp: 20px;
// PCPC25px


////////////////////
// 
////////////////////

$base-font-family: "", "Yu Gothic", YuGothic, " Pro", "Hiragino Kaku Gothic Pro", "",
Meiryo, "MS P", "MS PGothic", sans-serif;
$second-font-family: "", "Yu Mincho", YuMincho, " Pro", "Hiragino Mincho Pro", "MS P", "MS PMincho", serif;
// 

body {
  font-family: $base-font-family;
}

// 
$regular: 300;
$normal: 400;
$bold: 700;

////////////////////
// 
////////////////////

//
$black: #111;
$white: #fff;
$blue:#3084BD;
$bg-blue:#0068AE;
$green:#52B6CF;
$letter:#0C68AE;


////////////////////
// 
////////////////////

//
$breakpoints: (
  se: 320,
  x: 375,
  ssm: 426,
  sm: 600,
  md: 768,
  lg: 1024,
  lg02: 1290,
  xl: 1440,

);


//
$mediaquerys: (
  se: "screen and (max-width: #{map-get($breakpoints,'se')}px)",
  x: "screen and (max-width: #{map-get($breakpoints,'x')}px)",
  ssm: "screen and (max-width: #{map-get($breakpoints,'ssm')}px)",
  sm: "screen and (max-width: #{map-get($breakpoints,'sm')}px)",
  md: "screen and (max-width: #{map-get($breakpoints,'md')}px)",
  lg: "screen and (max-width: #{map-get($breakpoints,'lg')}px)",
  lg02: "screen and (max-width: #{map-get($breakpoints,'lg02')}px)",
  xl: "screen and (max-width: #{map-get($breakpoints,'xl')}px)",
);

// @include mq(){}md
@mixin mq($mediaquery: md) {
  @media #{map-get($mediaquerys, $mediaquery)} {
    @content;
  }
}

// rem16px10px1610
@function rem($pixels) {
  @return math.div($pixels, 16) * 1rem;
}


////////////////////
// 
////////////////////

//768px
.sp {
  display: none;
  @include mq("sm") {
    display: block;
  }
}

//sp
.pc{
  @include mq("sm") {
    display: none;
  }
}
