@use "global" as *;

.l-mv {
	padding-top: 100px;

	@include mq(ssm) {
		padding-top: 50px;
	}
}

.p-mv {
	position: relative;

	// MV
	&::before {
		content: "";
		position: absolute;
		background-image: url(../images/common/header-bg02.png);
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		width: 590px;
		height: 594.81px;
		top: 0;
		left: 0;
		z-index: -1;

		@include mq(lg02) {
			background-image: url(../images/common/mv-bg-sp.png);
			width: 249.22px;
			height: 251.26px;
		}
	}

	&__inner {
		width: 92.18%;
		margin-left: auto;
	}
	&__img {
		// width: 100%;
		// position: relative;

		// &::before {
		// 	content: "";
		// 	display: block;
		// 	padding-top: 67.8%;

		// 	@include mq(ssm) {
		// 		padding-top: 177.6%;
		// 	}
		// }

		img {
			// position: absolute;
			// top: 0;
			// left: 0;
			// width: 100%;
			// height: 100%;
			// object-fit: cover;
		}

		// MV
		&::after {
			content: "";
			position: absolute;
			background-image: url(../images/common/mv-bg.png);
			background-size: cover;
			background-repeat: no-repeat;
			// background-position: center;
			width: 800px;
			height: 100%;
			bottom: -45px;
			right: 0;
			z-index: 1;

			@include mq(lg02) {
				width: 250px;
			}
		}
	}

	&__title-box {
		width: 70%;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		text-align: center;
		z-index: 2;

		@include mq(ssm) {
			top: 40%;
			left: 55%;
		}
		@include mq(se) {
			top: 48%;
		}
	}
	&__logo {
		width: 305px;
		height: 212px;
		margin: 0 auto;
		display: block;
		position: relative;
		z-index: 201;

		@include mq(lg02) {
			height: 165px;
			width: 230px;
		}
		@include mq() {
			height: 100px;
			width: 150px;
		}
		@include mq(ssm) {
			height: 131px;
			width: 190px;
		}
		@include mq(se) {
		}
	}
	&__title {
		font-size: 39px;
		font-weight: $bold;
		margin-top: 78px;
		color: $white;

		@include mq(lg02) {
			font-size: 20px;
			margin-top: 24px;
		}

		@include mq(ssm) {
			font-size: 18px;
			margin-top: 15px;
		}
		@include mq(se) {
			font-size: 16px;
			margin-top: 15px;
		}
	}

	////////
	&__banner {
		display: block;
		width: 30%;
		background: rgba(255, 255, 255, 0.8);
		border-radius: 10px;
		position: absolute;
		bottom: 50px;
		left: 10vw;
		padding: 20px 0;
		z-index: 50;

		@include mq() {
			bottom: 10px;
			width: 40%;
		}
		@include mq(sm) {
			width: 40%;
			bottom: 5px;
		}
		@include mq(ssm) {
			width: 65%;
			bottom: 40px;
			left: 12vw;
		}
		@include mq(se) {
			width: 65%;
			bottom: 10px;
			left: 12vw;
		}
	
		&:hover {
			background: rgba(255, 255, 255, 1);
		}

		.sub-ttl {
			font-size: 14px;
			font-family: $second-font-family;
			text-align: center;

			@include mq(sm) {
				font-size: 11px;
			}
		}
		.ttl {
			font-size: 20px;
			color: #0c68ae;
			font-weight: $bold;
			text-align: center;
			margin-top: 10px;
			
			@include mq(sm) {
				font-size: 16px;
				margin-top: 5px;
				
			}
		}
		.text {
			font-size: 13px;
			font-family: $second-font-family;
			text-align: center;
			margin-top: 10px;
			@include mq(sm) {
				font-size: 10px;
				margin-top: 5px;
			}
		}
		.btn {
			position: relative;
			font-size: 11px;
			width: 175px;
			font-family: $second-font-family;
			margin: 0 auto;
			margin-top: 15px;

			&::before {
				content: "";
				position: absolute;
				width: 100px;
				height: 4px;
				background-image: url(../images/common/arrow-black.png);
				background-repeat: no-repeat;
				background-size: cover;
				bottom: 5px;
				right: -5px;
			}
		}
	}

	///
	.scroll-down {
		display: inline-block;
		position: absolute;
		right: 10vw;
		bottom: -95px;
		z-index: 2;
		padding: 0px 10px 210px;
		overflow: hidden;
		color: #fff;
		font-size: 11px;
		// text-transform: uppercase;
		text-decoration: none;
		writing-mode: vertical-lr;
		letter-spacing: 0.5em;

		@include mq() {
			right: 0;
			padding: 0px 10px 170px;
		}

		&::after {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 1px;
			height: 220px;
			background: #fff;

			animation: sdl 1.5s cubic-bezier(1, 0, 0, 1) infinite;
		}
		@keyframes sdl {
			0% {
				transform: scale(1, 0);
				transform-origin: 0 0;
			}
			50% {
				transform: scale(1, 1);
				transform-origin: 0 0;
			}
			50.1% {
				transform: scale(1, 1);
				transform-origin: 0 100%;
			}
			100% {
				transform: scale(1, 0);
				transform-origin: 0 100%;
			}
		}
	}
}
// 

.opening-animation {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 200;
	background-color: #0068ae;
	width: 100%;
	height: 100vh;

	figure {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		img {
		}
	}
}

