@use "global" as *;
.privacy-policy-page {
    margin-top: 100px;
    margin-bottom: 100px;
    width: 900px;
    max-width: 90%;
    margin: 0 auto;
    font-family: $second-font-family;

    @include mq() {
        margin-top: 65px;
        margin-bottom: 85px;
    }
    &__inner {
    }
    &__sub-title {
        margin-top: 100px;
        // text-align: center;
        margin-bottom: 70px;
        letter-spacing: 0.08em;
        font-size: 14px;
        

        @include mq() {
            margin-top: 75px;
            margin-bottom: 40px;
            font-size: 13px;
        }
    }
    &__items {
        width: 920px;
        max-width: 100%;
        margin: 124px auto 0 ;
    }
    &__item {
        &:not(:last-child) {
            margin-bottom: 76px;

        }

        h2 {
            padding-bottom: 13.5px;
            font-size: 19px;
            letter-spacing: 0.08em;
            border-bottom: 1px solid #707070;
            font-weight: $normal;
            font-family: $base-font-family;
            font-weight:$bold;
            color: $letter;

            @include mq() {
                font-size: 15px;
                padding-bottom: 10px;
            }
        }
        p {
            letter-spacing: 0.05em;
            line-height: 1.71;
            font-size: 14px;
            padding-top: 20px;

            @include mq() {
                font-size: 13px;
            }
        }

        ul {
            font-size: 13px;
            line-height: 1.8;
            letter-spacing: 0.05em;

            li {
            }
        }
    }
}
.p-policy__sign{
    margin-top: 36px;
    font-size: 14px;
    letter-spacing: 0.05em;
    line-height: 1.7;
    
    span{
        display: block;

    }
}