@use "global" as *;

// 
.section-head {
    padding-top: 100px;
    width: 100%;

    // height: 590px;

    // 
    &::before {
        content: "";
        position: absolute;
        background-image: url(../images/common/header-bg02.png);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        width: 590px;
        height: 594.81px;
        top: 0;
        left: 0;
        bottom: 0;
        z-index: -1;

        @include mq(lg02) {
            background-image: url(../images/common/mv-bg-sp.png);
            width: 249.22px;
            height: 251.26px;
        }
    }
    // 
    &::after {
        content: "";
        position: absolute;
        background-image: url(../images/common/sec-bg-green.png);
        // background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        width: 304.6px;
        height: 305.96px;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: -1;

        @include mq(lg02) {
            background-image: url(../images/common/sec-bg-sp_green.png);
            background-position: bottom right;
        }
    }
}

.c-section-head {
    display: inline-block;
    margin-right: 75px;
    padding-top: 93px;
    position: relative;
    top: 0;
    left: 50%;
    text-align: end;

   
    @include mq(ssm) {
        left: 30%;
    }

    h1 {
        font-size: 36px;
        font-family: $second-font-family;
        letter-spacing: 0.05em;
        color: $blue;
        text-align: center;


        @include mq() {
            font-size: 25px;
        }
    }
    p {
        font-size: 13px;
        font-family: $second-font-family;
        letter-spacing: 0.05em;
        font-weight: $regular;
        color: #1693b8;

        @include mq(sm) {
            font-size: 13px;
        }
    }
}
.c-section-head--40 {
    display: inline-block;
    margin-right: 75px;
    padding-top: 93px;
    position: relative;
    top: 0;
    left: 40%;
    // transform: translateX(-50%);
    text-align: center;

    @include mq(sm) {
        margin-right: 0;
        left: 40%;
    }
    @include mq(ssm) {
        margin-right: 0;
        left: 20%;
    }
    h1 {
        font-size: 36px;
        font-family: $second-font-family;
        letter-spacing: 0.05em;
        color: $blue;
        text-align: center;

        @include mq() {
            font-size: 25px;
        }
        @include mq(se) {
            font-size: 20px;
        }
    }
    p {
        font-size: 13px;
        font-family: $second-font-family;
        letter-spacing: 0.05em;
        font-weight: $regular;
        color: #1693b8;
        text-align: end;
    }
}

// top
.c-section-head--top {
    display: inline-block;
    margin-right: 75px;

    @include mq() {
        margin-right: 0;
        text-align: center;

    }

    @include mq(sm) {
        margin-right: 0;
        display: block;
    }
    h2 {
        font-size: 36px;
        font-family: $second-font-family;
        letter-spacing: 0.05em;
        color: $blue;

        @include mq(sm) {
            font-size: 28px;
        }
    }
    p {
        font-size: 13px;
        font-family: $second-font-family;
        letter-spacing: 0.05em;
        margin-top: 8px;

        @include mq(sm) {
            text-align: center;
            margin-top: 0;
            font-size: 13px;
        }
    }
}

.c-section-head--white {
    margin-right: 65px;

    @include mq(sm) {
        text-align: center;
        margin-right: 0;
    }

    h2 {
        color: $white;
        font-size: 36px;
        font-family: $second-font-family;
        letter-spacing: 0.05em;
        color: $white;

        @include mq(sm) {
            font-size: 28px;
            color: $letter;
        }
    }
    p {
        font-size: 13px;
        font-family: $second-font-family;
        letter-spacing: 0.05em;
        color: $white;
        @include mq(sm) {
            font-size: 13px;
            color: $black;
        }
    }
}
