@use "global" as *;

.l-u-news-single {
    margin-bottom: -100px;
}
.p-u-news-single {
    padding-top: 85.5px;

    &__inner {
        max-width: 90%;
        margin: 0 auto;
    }

    &__btn {
        width: 200px;
    }
}

// 
.p-news-single {
    margin-top: 80px;

    &__contents {
        display: flex;
        justify-content: space-between;

        @include mq() {
            display: block;
        }
    }
    &__inner {
        width: 70%;

        @include mq() {
            width: 100%;
        }
    }
    &__meta {
    }
    &__date {
        font-size: 15px;
    }
    &__title {
        
        p {
            font-size: 25px;
            padding: 14px 0;
            border-bottom: 1px solid $black;

            @include mq() {
                font-size: 22px;
            }
        }
    }

    &__img {
        margin-top: 60px;
        padding-top: 71.7%;
        position: relative;

        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__text {
        margin-top: 60px;
        margin-bottom: 90px;
        letter-spacing: 0.05em;
        line-height: 1.71;
        font-size: 14px;
    }
    &__btn {
        margin-top: 60px;
    }

    // 
    &__sidebar {
        width: 30%;
        margin-top: 60px;
        margin-left: 7.8vw;

        @include mq() {
            width: 100%;
            margin-top: 0;
            margin-left: 0;
        }
    }
    &__sidebar-inner {
    }
    &__sidebar-content {
        padding-bottom: 50px;
    }
    &__sidebar-title {
        font-size: 17px;
        border-bottom: 1px solid $black;
        padding-bottom: 5px;

        @include mq() {
            font-size: 14px;
        }
        p {
            display: inline-block;
        }
    }
    &__sidebar-link {
        font-size: 13px;
        line-height: 1.8;
        a {
            display: block;
        }
    }
    &__btn {
        text-align: center;
        margin-bottom: 120px;
        width: 200px;
    }
}
