input[type=checkbox],
input[type=radio] {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

.checkbox__list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.blue {
  color: #0C68AE;
}

.checkbox__item {
  display: block;
  justify-content: center;
  text-align: center;
}

.checkbox__label {
  position: relative;
  font-size: 18px;
  font-weight: 400;
  line-height: 1;
  display: inline-block;
}
@media screen and (max-width: 768px) {
  .checkbox__label {
    font-size: 15px;
    padding: 5px 10px;
  }
}

.checkbox__label::before {
  content: "";
  display: inline-block;
  margin-right: 8px;
  width: 20px;
  height: 20px;
  background-color: #fff;
  border: 1px solid #111;
}
@media screen and (max-width: 768px) {
  .checkbox__label::before {
    width: 15px;
    height: 13px;
  }
}

input.form__input:checked ~ .checkbox__label-cat::before {
  background-color: #fff;
  border: 1px solid #111;
}

input.form__input:checked ~ .checkbox__label-cat::after {
  border-bottom: 2px solid #295b68;
  border-left: 2px solid #295b68;
  content: "";
  height: 8px;
  left: 23px;
  left: 23px;
  position: absolute;
  top: 50%;
  top: 15px;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  width: 15px;
}
@media screen and (max-width: 768px) {
  input.form__input:checked ~ .checkbox__label-cat::after {
    height: 5px;
    left: 13px;
    top: 35%;
    width: 10px;
  }
}

input.form__input:checked ~ .checkbox__label::before {
  background-color: #0C68AE;
  border: 1px solid #fff;
}

input.form__input:checked ~ .checkbox__label::after {
  border-bottom: 2px solid #f4f4f4;
  border-left: 2px solid #f4f4f4;
  content: "";
  height: 8px;
  left: 4px;
  top: 4px;
  position: absolute;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  width: 14px;
}
@media screen and (max-width: 768px) {
  input.form__input:checked ~ .checkbox__label::after {
    left: 12px;
    top: 6px;
  }
}

