@use "global" as *;
.l-footer {
    margin-top: 250px;
    position: relative;
    // 
    &::before {
        content: "";
        position: absolute;
        width: 880px;
        height: 100%;
        background-color: #0093b8;
        clip-path: polygon(100% 0, 0% 100%, 100% 100%);
        bottom: 0;
        right: 0;
        z-index: 1;

        @include mq(sm) {
            width: 539.75px;
            height: 587.55px;
        }
    }
}

///////
.p-banner {
    display: flex;
    position: relative;
    z-index: 1;
    font-family: $second-font-family;

    @include mq(sm) {
        flex-direction: column;
    }

    &__wrap {
        text-align: center;
        position: relative;
        width: 100%;

        &::before {
            content: "";
            display: block;
            padding-top: 30%;
        }

        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &:hover::after {
            background-color: rgba(0, 0, 0, 0.7);
            transition: ease 0.6s;
        }
        &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, 0.4);
            top: 0;
            left: 0;
            z-index: 1;
        }
        img {
        }
        p {
            font-size: 22px;
            color: $white;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background-image: url(../images/common/banner-line.png);
            // background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            width: 220px;
            height: 68px;
            line-height: 68px;
            z-index: 2;
            letter-spacing: 0.2em;

            @include mq(){
                font-size: 18px;
            }
        }
    }
}

//////
.p-footer {
    padding-top: rem(60);
    padding-bottom: rem(40);
    background-color: $bg-blue;
    color: $white;

    &__content {
        display: flex;
        width: 90%;
        margin-bottom: rem(45);
        margin: 0 auto;

        @include mq(sm) {
            justify-content: center;
        }
    }
    //////
    &__left {
        width: 50%;
        padding-left: 10%;

        @include mq(sm) {
            width: 100%;
            padding-left: 0;
            text-align: center;
        }
    }
    &__info {
        font-family: $second-font-family;
        padding-right: 20px;
        position: relative;
        z-index: 50;
    }
    &__logo {
        width: 152px;
        margin-bottom: 43px;

        @include mq(sm) {
            margin: 0 auto;
            width: 137px;
            font-size: 12px;
        }

        img {
        }
    }

    &__adress {
        margin-bottom: 10px;
        font-size: 12px;
        line-height: 1.6;
        letter-spacing: 0.08em;

        @include mq(sm) {
            margin-top: 20px;
        }

        p {
            @include mq(sm) {
                font-size: 12px;
            }
            span {
                padding-right: rem(12);
            }
        }
    }

    &__googlmap {
        // letter-spacing: 0.13em;
        // text-align: center;
        font-size: 12px;
        margin-bottom: 24px;
        position: relative;
        padding-bottom: 5px;
        display: block;

        &:hover {
            opacity: 0.6;
        }

        &::before {
            position: absolute;
            content: "";
            bottom: 0;
            left: 0;
            width: 20%;
            height: 1px;
            background-color: $white;

            @include mq(sm) {
                bottom: 0%;
                left: 50%;
                transform: translateX(-50%);
            }
        }

        a {
        }
    }
    &__tel {
        margin-bottom: rem(10);
        // letter-spacing: 0.13em;
        font-size: 17px;
        font-family: $base-font-family;

        p {
            font-weight: $bold;

            @include mq(sm) {
                font-size: 17px;
            }
            span {
                padding: 5px;
                border: 1px solid $white;
                font-size: 10px;
                margin-right: 20px;
            }
        }
    }
    &__mail {
        margin-bottom: rem(22);
        // letter-spacing: 0.13em;
        font-size: 14px;
        p {
            @include mq(sm) {
                font-size: 12px;
            }
            span {
                padding-right: rem(12);
            }
        }
    }

    //////
    &__right {
        width: 50%;
        position: relative;
        z-index: 1;

        @include mq(sm) {
            display: none;
        }
    }

    &__sitemap {
    }
    &__sitemap-title {
        font-size: 13px;
        // letter-spacing: 0.29em;
        // font-family: $second-font-family;
        padding-bottom: rem(10);
        margin-bottom: rem(20);
        position: relative;

        &::before {
            position: absolute;
            content: "";
            width: 85%;
            height: 1px;
            background-color: $white;
            top: 40%;
            left: 60px;
        }

        p {
        }
    }
    &__sitemap-wrap {
        display: flex;
    }
    &__sitemap-left {
        li {
            font-size: 10px;
            margin-bottom: rem(12);
            a {
                &:hover {
                    opacity: 0.6;
                }
                span {
                    font-size: 13px;
                    padding-right: rem(12);
                }
            }
        }
    }
    &__sitemap-service-contents {
        margin-top: rem(10);
        position: relative;

        li {
            padding-left: rem(20);
            a {
                &:hover {
                    opacity: 0.6;
                }
            }
        }
    }

    &__sitemap-right {
        padding-left: 50px;

        li {
            font-size: 10px;
            margin-bottom: rem(12);
            a {
                &:hover {
                    opacity: 0.6;
                }

                span {
                    font-size: 13px;
                    padding-right: rem(12);
                }
            }
        }
    }

    &__facebook {
        img {
        }
    }

    /////////
    &__copyright {
        // color: $white;
        text-align: center;
        letter-spacing: 0.52em;
        opacity: 63%;
        font-size: 10px;
        position: relative;
        z-index: 1;
        padding-left: $padding-sp;
        padding-right: $padding-sp;

        @include mq(sm) {
            font-size: 7px;
            letter-spacing: 0.4em;
        }
        p {
            span {
                // padding-right: 5px;
            }
        }
    }
}

// 
.page_top {
    // width: 70px;
    // height: 50px;
    position: fixed;
    right: 80px;
    bottom: 25px;
    opacity: 0;
    text-align: center;
    z-index: 99;
    transition: all 0.8s ease 0s;

    span {
        color: $white;
        margin-top: 5px;
        font-size: 10px;
    }

    &.is-show {
        opacity: 1;
    }

    @include mq() {
        right: 15px;
    }
    @include mq(ssm) {
        bottom: 30px;
    }
}
.page_top a {
    position: relative;
    display: block;
    width: 50px;
    height: 50px;
    text-decoration: none;
    background: #0068ae;
    border-radius: 10%;
    margin: 0 auto;

    @include mq() {
        width: 40px;
        height: 40px;
    }
}
.page_top a::before {
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    content: "\f106";
    font-size: 35px;
    color: #fff;
    position: absolute;
    width: 25px;
    height: 25px;
    top: -27px;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
    text-align: center;

    @include mq() {
        top: -30px;
        left: 0px;
    }
}
