@use "global" as *;

.l-u-tolesabi01 {
	padding-top: 100px;
	// padding-bottom: 143px;
}

///sec01///
.p-u-tolesabi01 {
	&__inner {
	}
	&__wrap {
		display: flex;
		flex-direction: row-reverse;
		margin-top: 70px;
		margin: 0 calc(50% - 50vw);
		width: 100vw;
		overflow-x: hidden;

		position: relative;
		z-index: 1;
		align-items: center;

		@include mq() {
			display: block;
			margin-top: 25px;
			width: 80%;
			margin: 0 auto;
			padding-left: $padding-sp;
			padding-right: $padding-sp;
		}
		@include mq(ssm) {
			display: block;
			margin-top: 25px;
			width: 100%;
			margin: 0 auto;
			padding-left: 0;
			padding-right: 0;
		}
	}
	&__img {
		flex: 0 0 40%;
		position: relative;

		&::before {
			content: "";
			padding-top: 77%;
			display: block;
		}

		@include mq() {
			width: 70%;
			margin-right: auto;
			margin-left: auto;
		}
		@include mq(sm) {
			width: 100%;
			margin-right: auto;
			margin-left: auto;
		}

		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__wrap--reverse {
		display: flex;
		margin-top: 70px;
		margin: 0 calc(50% - 50vw);
		width: 100vw;
		overflow-x: hidden;

		position: relative;
		z-index: 1;
		align-items: center;
		margin-top: 110px;

		@include mq() {
			margin-top: 41px;
			display: block;
			// width: 70%;
			// margin: 0 auto;
			// padding-left: $padding-sp;
			// padding-right: $padding-sp;
		}
	}

	&__text {
		padding-right: 83px;
		padding-left: 300px;

		@include mq(xl) {
			padding-left: 100px;
		}
		@include mq() {
			padding-right: 0;
			padding-left: 0px;
		}
		h3 {
		}
		p {
			letter-spacing: 0.05em;
			line-height: 1.714;
			margin-top: 52px;

			@include mq() {
				margin-top: 15px;
			}
		}
	}
	&__text--reverse {
		padding-right: 300px;
		padding-left: 100px;

		@include mq(xl) {
			padding-right: 100px;
		}
		@include mq() {
			padding-right: 30px;
			padding-left: 30px;
			// margin-right: 0;
		}

		@include mq(ssm) {
			padding-right: $padding-sp;
			padding-left: $padding-sp;
		}
		h3 {
		}
		p {
		}
	}
	&__img--reverse {
		flex: 0 0 40%;
		position: relative;

		&::before {
			content: "";
			padding-top: 108%;
			display: block;

			@include mq() {
				padding-top: 78%;
			}
		}

		@include mq() {
			width: 70%;
			margin-right: auto;
		}

		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}

////sec02/////

.l-u-tolesabi02 {
	padding-top: 120px;
	margin-bottom: -250px;

	@include mq() {
		padding-top: 93px;
	}
}

.p-u-tolesabi02 {
	&__inner {
	}
	&__title {
		font-size: 22px;
		font-weight: $bold;
		text-align: center;

		@include mq() {
			font-size: 17px;
		}
	}
	&__icons {
		margin-top: 110px;
		margin-bottom: 96px;

		@include mq() {
			margin-top: 40px;
		}
	}
	&__icon {
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(192px, 1fr));
		gap: 61px 92px;

		@include mq() {
			display: block;
			width: 50%;
			margin: 0 auto;
		}
		@include mq(ssm) {
			display: block;
			width: 100%;
			margin: 0 auto;
		}

		@include mq() {
			img {
				&:not(:first-child) {
					margin-top: 46px;
				}
			}
		}
	}
/////////////////
	.pt10{
		padding-top: 10px;
	}
	.pt25{
		padding-top: 25px;
	}
	.pt20{
		padding-top: 20px;
	}


	/////tolesabi-modal////

	&__modal-title {
		display: block;
		padding-top: 73px;
		font-size: 23px;
		position: relative;

		@include mq() {
			font-size: 15px;
		}

		&::before {
			position: absolute;
			content: "";
			background-color: $white;
			width: 6px;
			height: 34px;
			bottom: 0%;
			left: 0;
			// transform: translateY(-50%);
		}
	}
}
