@use "global" as *;
.p-u-contact {
    margin-bottom: -134px;
    position: relative;
    z-index: 50;
}
.p-contact-page {
    &__inner {
    }
    &__title {
        @include mq() {
        }
    }
    &__text {
        text-align: center;
        margin-top: 115px;

        @include mq() {
            margin-top: 70px;
            font-size: 14px;
        }
    }

    &__container {
        width: 900px;
        max-width: 100%;
        margin: 0 auto;
    }
    &__tel {
    }

    &__tel-title {
        text-align: center;
        letter-spacing: 0.21em;
        font-size: 27px;
        margin-top: 68px;
        color: $white;
        background-color: $letter;
        padding: 10px 0;

        @include mq() {
            margin-top: 40px;
            font-size: 15px;
            // background-color: $bg;
            padding: 10px;
        }
    }
    
    &__tel-text {
        text-align: center;
        letter-spacing: 0.08em;
        font-size: 15px;
        padding-top: 20px;
        display: block;

        @include mq() {
            font-size: 10px;
        }
    }
    &__mail {
    }
    &__mail-title {
        text-align: center;
        letter-spacing: 0.21em;
        font-size: 27px;
        margin-top: 105px;
        color: $white;
        padding: 10px 300px;
        background-color: $letter;

        @include mq() {
            padding: 10px 10px;
            margin-top: 60px;
            font-size: 15px;
        }
    }
    &__mail-text {
        margin-top: 45px;
        text-align: center;
        letter-spacing: 0.08em;
        font-size: 17px;

        @include mq() {
            font-size: 12px;
            margin-top: 20px;
        }
    }

    // contact-form
    &__c-form {
        margin-top: 70px;

        @include mq() {
            margin-top: 10px;
        }
    }
    &__c-form-text {
        text-align: start;
        padding-bottom: 10px;
        font-weight: 400;
        font-size: 15px;

        @include mq() {
            font-size: 11px;
        }

        span {
            font-size: 15px;
            color: #e73828;

            @include mq() {
                font-size: 13px;
            }
        }
    }
    &__c-form-inner {
        margin-top: 37px;

        label {
            display: flex;
            justify-content: center;
            align-self: center;

            @include mq() {
                flex-direction: column;
            }
        }

        label:not(:last-child) {
            margin-bottom: 20px;
        }

        .title {
            display: flex;
            align-self: center;
            // padding-right: 20px;
            width: 205px;
            font-weight: 400;
            font-size: 19px;

            @include mq() {
                align-self: flex-start;
                font-size: 13px;
            }
        }

        .title-textarea {
            display: flex;
            align-self: center;
            width: 205px;
            font-weight: 400;
            font-size: 19px;

            @include mq() {
                align-self: flex-start;
                font-size: 13px;
            }
        }

        .required {
            padding-left: 3px;
            font-size: 10px;
            color: #e73828;
        }

        .input-form,
        textarea {
            display: block;
            padding: 10px 20px;
            flex: 1;
            border: 1px solid #c3c3c3;
        }

        textarea {
            height: 200px;
        }
    }
}

.tel-num {
    text-align: center;
    letter-spacing: 0.1em;
    font-size: 46px;
    color: $letter;
    margin-top: 47px;
    font-weight: $bold;

    @include mq() {
        font-size: 24px;
    }
    @include mq(se) {
        font-size: 18px;
    }
}

// 
input[type="checkbox"],
input[type="radio"] {
    position: absolute;
    white-space: nowrap;
    width: 1px;
    height: 1px;
    overflow: hidden;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    margin: -1px;
}

.checkbox__list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

.blue {
    color: $letter;
}

.checkbox__item {
    display: block;
    justify-content: center;
    text-align: center;
}

.checkbox__label {
    // display: flex;
    // justify-content: center;
    // align-items: center;
    position: relative;
    // padding: 10px 20px;
    font-size: 18px;
    font-weight: 400;
    line-height: 1;
    // text-align: center; 
    display: inline-block;   

    @include mq() {
        font-size: 15px;
        padding: 5px 10px;
    }
}

.checkbox__label::before {
    content: "";
    display: inline-block;
    margin-right: 8px;
    width: 20px;
    height: 20px;
    background-color: #fff;
    border: 1px solid $black;

    @include mq() {
        width: 15px;
        height: 13px;
    }
}

input.form__input:checked ~ .checkbox__label {
    // color: #f4f4f4;
    // background-color: #053e62;
}

input.form__input:focus ~ .checkbox__label {
    // border: 3px solid #0277b4;
    // box-shadow: 0 0 8px #0277b4;
}

// 
input.form__input:checked ~ .checkbox__label-cat::before {
    background-color: $white;
    border: 1px solid $black;
}

input.form__input:checked ~ .checkbox__label-cat::after {
    border-bottom: 2px solid #295b68;
    border-left: 2px solid #295b68;
    content: "";
    height: 8px;
    left: 23px;
    left: 23px;
    position: absolute;
    top: 50%;
    top: 15px;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    width: 15px;

    @include mq() {
        height: 5px;
        left: 13px;
        top: 35%;
        width: 10px;
    }
}

///
input.form__input:checked ~ .checkbox__label::before {
    background-color: $letter;
    border: 1px solid $white;
}

input.form__input:checked ~ .checkbox__label::after {
    border-bottom: 2px solid #f4f4f4;
    border-left: 2px solid #f4f4f4;
    content: "";
    height: 8px;
    left:4px;
    top: 4px;
    position: absolute;
    -webkit-transform: rotate(-45deg);
    transform:  rotate(-45deg);
    width: 14px;

    @include mq() {
        left: 12px;
        top: 6px;
      
    }
    // @include mq(sm) {
    //     left: 164px;
    // }
    // @include mq(ssm) {
    //     left: 85px;
    // }
    // @include mq(ssm) {
    //     left: 85px;
    // }
    // @include mq(x) {
    //     left: 62px;
    // }
    // @include mq(se) {
    //     left: 38px;
    // }
}

// submit
.submit-btn {
    position: relative;
    z-index: 100;
    text-align: center;
    font-size: 16px;

    @include mq() {
        font-size: 14px;
    }
}
.submit-wrap {
    position: relative;

    &::after {
        border-color: #fff;
        border-style: solid;
        border-width: 0 0 30px 30px;
        content: "";
        position: absolute;
        right: -15px;
        top: 40px;
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);

        @include mq() {
            top: 65px;
        }
    }
    &::before {
        border-color: #fff;
        border-style: solid;
        border-width: 0 0 30px 30px;
        content: "";
        left: -15px;
        position: absolute;
        top: -15px;
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);
        z-index: 1;

        @include mq() {
            top: 15px;
        }
    }
}
.submit {
    display: inline-block;

    @include mq() {
        display: flex;
        justify-content: center;
    }

    &__form {
        background-color: $blue;
        font-family: $second-font-family;
        color: $white;
        padding: 15px 70px;
        border: none;
        display: block;
        margin-top: 30px;
        position: relative;

        &:hover {
            background-color: $green;
            transition: ease 0.8s;
        }
    }
    &__label {
    }
}

.checkbox__text {
    text-align: center;
    font-size: 15px;
    margin-top: 75px;
    margin-bottom: 20px;
}
