// 
input[type="checkbox"],
input[type="radio"] {
    position: absolute;
    white-space: nowrap;
    width: 1px;
    height: 1px;
    overflow: hidden;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    margin: -1px;
}

.checkbox__list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

// .blue {
//     color: $letter;
// }

.checkbox__item {
    display: block;
    justify-content: center;
}

.checkbox__label-cat {
    display: flex;
    align-items: center;
    position: relative;
    padding: 10px 20px;
    font-weight: 400;
    line-height: 1;

    @include mq() {
        font-size: 11px;
        padding: 5px 10px;
    }
}

.checkbox__label-cat::before {
    content: "";
    display: inline-block;
    margin-right: 8px;
    width: 20px;
    height: 20px;
    background-color: #fff;
    border: 1px solid $black;

    @include mq() {
        width: 15px;
        height: 15px;
    }
}

input.form__input:checked ~ .checkbox__label {
    // color: #f4f4f4;
    // background-color: #053e62;
}

input.form__input:focus ~ .checkbox__label {
    // border: 3px solid #0277b4;
    // box-shadow: 0 0 8px #0277b4;
}

input.form__input:checked ~ .checkbox__label-cat::before {
    background-color: $letter;
    border: 1px solid $white;
}

input.form__input:checked ~ .checkbox__label::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 23px;
    border-left:2px solid #ff0000;
	border-bottom:2px solid #ff0000;
	width:6px;
	height:3px;
	-webkit-transform:rotate(-45deg);
	transform:rotate(-45deg);
    left:4px;
    top: 4px;
    
    // @include mq() {
    //     border-bottom: 2px solid #f4f4f4;
    //     border-left: 2px solid #f4f4f4;

    //     top: 45%;
    //     left: 10px;
    // }
}

// submit
.submit-btn {
    position: relative;
    z-index: 100;
}
.submit-wrap02 {
    position: relative;

    &::after {
        border-color: #fff;
        border-style: solid;
        border-width: 0 0 30px 30px;
        content: "";
        position: absolute;
        right: -15px;
        top: 40px;
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);

        @include mq() {
            top: 65px;
        }
    }
    &::before {
        border-color: #fff;
        border-style: solid;
        border-width: 0 0 30px 30px;
        content: "";
        left: -15px;
        position: absolute;
        top: -15px;
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);
        z-index: 1;

        @include mq() {
            top: 15px;
        }
    }
}
.submit {
    display: inline-block;

    @include mq() {
        display: flex;
    }

    &__form {
        background-color: $blue;
        font-family: $second-font-family;
        color: $white;
        padding: 15px 70px;
        border: none;
        display: block;
        margin-top: 30px;
        position: relative;

        &:hover {
            background-color: $green;
            transition: ease 0.8s;
        }
    }
    &__label {
    }
}

.checkbox__text {
    text-align: center;
    font-size: 15px;
    margin-top: 75px;

    @include mq() {
        font-size: 13px;
        margin-top: 30px;
    }
}
.checkbox__cat {
    font-size: 19px;
    margin-top: 55px;

    span {
        font-size: 13px;
        margin-left: 16px;
    }
}
