@use "global" as *;

// top
.c-item-text--top {
    font-family: $second-font-family;
    letter-spacing: 0.05em;
    color: $white;

    span {
        display: block;
        font-size: 17px;
    }

    figure {
        img {
        }
    }

    h3 {
        text-align: center;
        font-size: 24px;
        margin-top: 40px;
        line-height: 1.7;

        @include mq() {
            font-size: 21px;
        }
    }
    p {
        @include mq() {
            font-size: 14px;
        }
    }

    &__more-btn {
        margin-top: 40px;
    }
}

// 
.mt-small {
    margin-top: 25px;
}
.mt-large {
    margin-top: 54px;
}

// 
.c-item-text--reverse{
margin-right: 200px;
font-family: $second-font-family;
    letter-spacing: 0.05em;
 
    @include mq(xl){
        margin-right: 0;

    }

    span {
        display: block;
        font-size: 18px;

        @include mq(sm) {
            font-size: 14px;
        }
    }
    figure {
        width: 62px;
        margin: 0 auto;

        img {
        }
    }

    h3 {
        font-size: 28px;
        margin-top: 40px;
        line-height: 2;
        text-align: center;

        @include mq(sm) {
            font-size: 24px;
        }
    }

    p {
        margin-top: 44px;
        font-size: 15px;

        @include mq() {
            font-size: 13px;
            margin-top: 30px;
        }
    }

    &__more-btn {
        width: 200px;
        margin: 48px auto 0;

        // @include mq(sm){
        //     margin: 50px auto 0;
        // }

        a {
            display: block;
        }
    }
    &__more-btn02 {
        width: 200px;
        margin: 100px auto 0;

        @include mq(sm){
            margin: 50px auto 0;
        }

        a {
            display: block;
        }
    }


    &__more-btn--large {
        width: 200px;
        margin: 40px auto 0;

        a {
            display: block;
        }
    }
    &__more-btn--small {
        width: 200px;
        margin: 48px auto 0;

        a {
            display: block;
        }
    }

}


