// 
.large {
    width: 100px;
    margin: 0 auto;
}
.small {
    width: 50px;
    margin: 0 auto;
}

 
.c-item-text__more-btn--large,
.c-item-text__more-btn,
.c-item-text__more-btn--small {
    position: relative;
    z-index: 50;
    transition: ease 0.6s;
    &:hover {
        transform: translateX(15px);
    }
}
