@use "global" as *;

.l-u-service01 {
	padding-top: 85px;
	padding-bottom: 150px;

	@include mq(){
		padding-bottom: 100px;

	}
}

.p-servce-head {
	// margin-bottom: 85px;
}
.p-u-service01 {
	&__inner {
	}
	&__wrap {
		display: flex;

		@include mq(sm) {
			flex-direction: column-reverse;
		}
	}
	&__img {
		flex: 0 0 50%;
		margin-left: 72px;

		@include mq() {
			margin-left: 0px;
			margin-bottom: 20px;
		}
		img {
		}
	}
	&__text {
		margin-top: 58px;
		letter-spacing: 0.05em;
		line-height: 1.4;
		font-family: $second-font-family;
		font-size: 14px;

		@include mq() {
			font-size: 13px;
			margin-top: 33px;
		}
	}
	&__illustrated {
		margin-top: 75px;

		@include mq() {
			margin-top: 25px;
		}

		img {
		}
	}
}

.l-u-service02 {
	// padding-top: 85px;
	padding-bottom: 110px;
	margin-bottom: -250px;
	@include mq() {
		padding-bottom: 150px;
	}
}
.p-u-service02 {
	&__inner {
		position: relative;
		z-index: 50;
	}
	&__wrap {
		display: flex;
		flex-direction: row-reverse;

		@include mq() {
			flex-direction: column-reverse;
		}
	}
	&__img {
		margin-right: 72px;

		@include mq() {
			margin-bottom: 30px;
			margin-right: 0px;
		}
		img {
		}
	}

	&__menu {
		padding-top: 80px;
		// width: 990px;
		// max-width: 90%;

		@include mq() {
			padding-top: 56px;
		}
		@include mq(sm) {
			width: 100%;
		}
	}
	&__menu-title {
		font-size: 16px;
		font-weight: bold;
		position: relative;

		&::before {
			position: absolute;
			content: "";
			width: 90%;
			height: 1px;
			bottom: 0;
			left: 0;
			background-color:$black;
		}

		@include mq() {
			text-align: center;
		}
		span {
			font-size: 24px;
			font-weight: $bold;
			color: $letter;

			@include mq() {
				display: block;
			}
		}
	}
	&__menu-items {
		margin-top: 58px;

		li {
			display: flex;
			// flex-wrap: wrap;

			@include mq(sm) {
				flex-direction: column;
			}
			&:not(:first-child) {
				margin-top: 45px;
			}

			P {
				width: 80%;
				@include mq() {
					font-size: 14px;
				}
				@include mq(sm) {
					margin-top: 20px;
					width: 100%;
				}

				line-height: 1.6;
				span {
					color: #1693b8;
				}
			}
		}
	}
	&__menu-img {
		width: 25%;
		margin-right: 34px;
		@include mq() {
			width: 30%;
		}
		@include mq(sm) {
			width: 100%;
		}
	}
}

#item01,
#item02 {
	margin-top: -150px;
	padding-top: 150px;
}
