@use "global" as *;

.l-concept {
	padding-top: 120px;
}

.p-concept {
	&__inner {
		width: 980px;
		max-width: 100%;
		margin: 0 auto;

		@include mq() {
			width: 100%;
		}
	}

	&__explanation {
		width: 90%;
		margin: 75px auto 0;

		@include mq() {
			width: 100%;
		}
	}
	&__logo {
		width: 290px;
		margin: 0 auto;
	}
	&__illustrated {
		width: 900px;
		max-width: 90%;
		margin: 45px auto 0;

		@include mq() {
			width: 100%;
			margin-top: 45px;
		}
	}
	&__text {
		margin-top: 60px;
		padding-left: $padding-sp;
		padding-right: $padding-sp;
	}
	&__btn {
		margin: 63px auto 0;

		@include mq() {
			margin: 24px auto 0;
		}
	}
}
