@use "global" as *;

.l-know-how {
	margin-top: 143px;
	padding: 100px 0 124px;

	@include mq(sm) {
		margin-top: 0px;
		padding-bottom: 0;
	}
}

.p-know-how {
	background-color: $green;

	@include mq(sm) {
		background-color: $white;
	}
	&__inner {
		width: 1080px;
		max-width: 90%;
		margin: 0 auto;
	}
	&__head-box {
		display: flex;
		justify-content: center;
		width: 980px;
		max-width: 90%;
		margin: 0 auto;

		@include mq(sm) {
			flex-direction: column;
			align-items: center;
		}
	}
	&__cards {
		display: grid;
		justify-content: center;
		grid-template-columns: 32% 32% 32%;
		gap: 30px;
		margin-top: 28px;
		color: $white;

		@include mq(sm) {
			display: block;
		}
	}

	&__card {

		@include mq(sm){
			&:not(:first-child) {
				margin-top: 30px;
			}

        }
	}

	&__card-body {
		background-color: $blue;
		padding: 36px;
	}
}

.action {
	&:hover {
		transform: translateY(-10px);
	}
}

#know-how {
	padding-top: 200px;
	margin-top: -200px;

	@include mq() {
		padding-top: 100px;
		margin-top: -100px;
	}
}
