@use "global" as *;

////tab///
// 
.p-service-tab {
	margin-top: 60px;

	&__nav {
		display: flex;
		justify-content: center;

		@include mq() {
			flex-direction: column;
		}
	}
	&__nav-item {
		width: 100%;
		background-color: $blue;
		color: $white;
		padding: 20px 30px 20px 20px;
		margin-right: 10px;
		text-align: center;
		font-size: 22px;
		font-weight: $bold;
		display: inline-block;
		opacity: 0.7;

		@include mq() {
			padding: 10px 15px 15px 10px;
			margin: 0 auto 20px;
			font-size: 13px;
		}
	}
	&__nav-item.is-active {
		// background-color: $base;
		opacity: 1;
	}

	.contents01,
	.contents02,
	.contents03 {
		position: relative;

		&::after {
			position: absolute;
			right: -20px;
			top: -20px;
			// width: 20px;
			// height: 20px;
			content: "";
			border-style: solid;
			border-width: 0 0 40px 40px;
			border-color: $white;
			transform: rotate(45deg);

			@include mq() {
				border-width: 0 0 30px 30px;
				right: -15px;
				top: -15px;
			}
		}
	}
	.contents01 {
		&::before {
			content: "";
			display: inline-block;
			position: relative;
			top: 5px;
			left: -8px;
			width: 30px;
			height: 30px;
			background-image: url(../images/common/logo-post.png);
			background-repeat: no-repeat;
			background-size: contain;

			@include mq() {
				width: 25px;
				height: 25px;
			}
		}
	}
	.contents02 {
		&::before {
			content: "";
			display: inline-block;
			position: relative;
			top: 10px;
			left: -15px;
			width: 30px;
			height: 30px;
			background-image: url(../images/common/logo-movie.png);
			background-repeat: no-repeat;
			background-size: contain;

			@include mq() {
				width: 25px;
				height: 25px;
			}
		}
	}
	.contents03 {
		&::before {
			content: "";
			display: inline-block;
			position: relative;
			top: 8px;
			left: -8px;
			width: 40px;
			height: 30px;
			background-image: url(../images/common/logo-column.png);
			background-repeat: no-repeat;
			background-size: contain;

			@include mq() {
				width: 30px;
				height: 25px;
			}
		}
	}
	&__content {
		// display: none;
		margin-top: 50px;
	}
}

