.panel-group {
	margin-bottom: -100px;
}

.panel {
	display: none;

	&__title {
		text-align: center;
		// margin-top: 50px;

		h3 {
			font-size: 22px;
			font-weight: $bold;
			margin-top: 46px;

			@include mq(sm) {
				font-size: 18px;
			}
		}
	}
	&__logo {
		width: 40px;
		margin: 0 auto;
	}
}
.tab.is-active {
	background: $blue;
	color: #fff;
	transition: all 0.2s ease-out;
}
.panel.is-show {
	display: block;
}

