// CSS
.p-post-cards {
	width: 900px;
	max-width: 100%;
	margin: 0 auto;
	margin-top: 89px;
	display: flex;
	flex-wrap: wrap;
	padding-left: $padding-sp;
	padding-right: $padding-sp;

	@include mq(sm) {
		flex-direction: column;
	}
}

.p-post-card {
	width: calc(50% - 100px * 1 / 2);

	@include mq(sm) {
		width: 100%;
	}

	&:not(:nth-child(2n + 1)) {
		margin-left: 100px;

		@include mq(sm) {
			margin-left: 0;
		}
	}
	&:nth-child(n + 3) {
		margin-top: 97px;

		@include mq(sm) {
			margin-top: 0;
		}
	}

	@include mq(sm){
		&:not(:first-child){
			margin-top: 77px;
		}

	}

	&__img {
		position: relative;
		width: 100%;

		&::before {
			content: "";
			display: block;
			padding-top: 65%;
		}
		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	&__body {
	}
	&__wrap {
		display: flex;
		// align-items: flex-end;
		padding: 14px 0;
		border-bottom: 1px solid #000;
	}
	&__meta {
		font-size: 14px;
	}
	&__date {
	}
	&__customer {
	}
	&__tag {
		margin-left: 12px;

		.tag01,
		.tag02,
		.tag03 {
			color: $white;
			font-size: 12px;
			padding: 5px;
			border-radius: 7px;
			display: inline-block;
			margin-bottom: 5px;
		}

		.tag01 {
			background-color: #1693b8;
		}
		.tag02 {
			background-color: #107773;
		}
		.tag03 {
			background-color: #0c68ae;
		}
	}
	&__title {
		padding-top: 22px;
		font-weight: $bold;
		font-size: 16px;

		@include mq() {
			font-size: 12px;
		}
	}
	&__text {
		margin-top: 34px;
		margin-bottom: 34px;
		font-size: 14px;
	}

	&__btn {
		margin: 0 auto;
	}
	&__pagenation {
		margin-top: 115px;
	}
}

