@use "global" as *;

.l-u-automation01 {
	padding-top: 200px;
	padding-bottom: 80px;

	@include mq() {
		padding-bottom: 60px;
		padding-top: 100px;
	}
}

///sec01///
.p-u-automation01 {
	&__inner {
	}
	&__wrap {
		display: flex;
		flex-direction: row-reverse;
		align-items: center;
		margin: 0 calc(50% - 50vw);
		width: 100vw;

		@include mq() {
			flex-direction: column-reverse;
			width: 70%;
			margin: 0 auto;
		}
		@include mq() {
			flex-direction: column-reverse;
			width: 100%;
			margin: 0 auto;
		}
	}

	&__text {
		padding-left: 73px;
		padding-right: 300px;
		font-family: $second-font-family;

		@include mq(xl) {
			padding-right: 100px;
		}

		@include mq() {
			padding-left: 0px;
			padding-right: 0px;
			margin-top: 46px;
		}

		h3 {
			font-size: 22px;
			font-weight: $bold;
		}
		p {
			margin-top: 52px;
			letter-spacing: 0.05em;
			line-height: 1.714;
			font-size: 16px;

			@include mq() {
				margin-top: 28px;
				font-size: 14px;
			}
		}
	}
	&__img {
		flex: 0 0 40%;

		@include mq() {
			flex: 0 0 0;
		}

		img {
		}
	}
}

////sec02/////
.l-u-automation02 {
	padding-top: 88px;
	padding-bottom: 93px;
	background-color: #97d3e2;

	@include mq() {
		padding-top: 75px;
		padding-bottom: 93px;
	}
}

.p-u-automation02 {
	&__inner {
		@include mq() {
			width: 70%;
			margin: 0 auto;
		}
		@include mq(sm) {
			width: 100%;
			margin: 0 auto;
		}
	}
	&__title {
		text-align: center;
		font-size: 22px;
		font-weight: $bold;

		@include mq() {
			font-size: 18px;
		}

		&:not(:first-child) {
			margin-left: 14px;
		}
	}

	&__grid01 {
		text-align: center;
		width: 70%;
		margin: 0 auto;
		font-size: 17px;

		@include mq() {
			width: 100%;
			text-align: start;
			font-size: 13px;
		}

		span {
			display: inline-block;

			padding: 8px 13px;
			border-radius: 10px;
			background-color: #c4e6f9;
			margin-top: 34px;
			font-size: 17px;

			@include mq() {
				font-size: 13px;
			}

			&:not(:first-child) {
				margin-left: 14px;

				@include mq() {
					margin-top: 20px;
					margin-left: 5px;
				}
			}
		}
	}
	&__grid02 {
		text-align: center;
		font-size: 13px;
		span {
			display: inline-block;

			padding: 8px 13px;
			border-radius: 10px;
			background-color: #c4e6f9;
			margin-top: 10px;

			&:not(:first-child) {
				margin-left: 14px;
			}
		}
	}
	&__img {
		margin-top: 50px;
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(265px, 1fr));
		gap: 80px 53px;
		text-align: center;

		@include mq() {
			gap: 45px 20px;
		}

		img {
		}
		span {
			font-size: 17px;
			margin-top: 12px;
			display: inline-block;
		}
	}
}

////sec03////

.p-u-automation03 {
	padding-top: 97px;
	margin-bottom: -250px;

	@include mq() {
		padding: 71px 20px 0;
	}

	&__inner {
	}
	&__wrap {
		display: flex;
		align-items: center;
		margin-top: 20px;
		margin-left: 56px;
		margin-bottom: 97px;
		// margin-right: 100px;

		@include mq() {
			flex-direction: column;
			margin-left: 0;
			align-items: flex-end;
			margin-bottom: 40px;
		}
		@include mq(ssm) {
			margin-bottom: 0px;
		}
	}
	&__img {
		flex: 0 0 40%;
		margin-left: 45px;
		margin-right: 100px;
		z-index: 30;

		@include mq() {
			margin: 0 calc(50% - 50vw);
			width: 70%;
			// margin-left: 0;
			margin-top: 37px;
		}

		img {
			// margin-left: auto;
			width: 100vw;
		}
	}

	&__text-area {
		padding-left: 300px;

		@include mq(xl) {
			padding-left: 100px;
		}
	}

	&__modal-title {
		display: block;
		padding-top: 73px;
		font-size: 23px;
		position: relative;

		@include mq() {
			font-size: 15px;
			padding-top: 174px;
		}
		&::before {
			position: absolute;
			content: "";
			background-color: $white;
			width: 6px;
			height: 34px;
			bottom: 0%;
			left: 0;

			@include mq() {
				bottom: -5%;
			}
		}
	}
}
