@use "global" as *;

.l-u-scada01 {
	padding-top: 100px;
	padding-bottom: 132px;

	@include mq() {
	}
}

///sec01///
.p-u-scada01 {
	&__inner {
	}
	&__wrap {
		display: flex;
		margin-top: 70px;
		margin: 0 calc(50% - 50vw);
		width: 100vw;
		overflow-x: hidden;
		padding-right: 300px;
		position: relative;
		z-index: 1;
		align-items: center;

		@include mq(xl) {
			padding-right: 166px;
		}

		@include mq() {
			display: block;
			padding-right: $padding-sp;
			padding-left: $padding-sp;
			position: relative;
		}
		@include mq(sm) {
			display: block;
			width: 100%;
			margin: 0 auto;
			position: relative;
		}
	}

	&__img {
		padding-right: 83px;
		flex: 0 0 50%;
		position: relative;
		&::before {
			content: "";
			display: block;
			padding-top: 108%;

			@include mq() {
				padding-top: 63%;
			}
		}

		@include mq() {
			padding-right: 0;
		}

		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__num {
		padding-left: $padding-sp;
		padding-right: $padding-sp;
	}
	&__num-title {
		padding-right: 0;
	}

	&__imgs {
		// display: flex;
		// margin: 0 calc(50% - 50vw);
		// width: 100vw;
		margin-top: 105px;

		@include mq() {
			margin-top: 35px;
		}

		img {
			// width: 20%;
		}
	}

	&__example-wrap {
		margin-top: 125px;
		display: flex;
		justify-content: center;
		padding: 35px 100px;
		border-top: 1px solid #707070;
		border-bottom: 1px solid #707070;
		position: relative;

		@include mq() {
			margin-top: 51px;
			flex-direction: column;
			padding: 35px 50px;
		}
		@include mq(sm) {
			margin-top: 51px;
			flex-direction: column;
			padding: 35px 25px;
		}

		&::after {
			position: absolute;
			content: "";
			width: 100%;
			height: 2px;
			background-color: #707070;
			top: -9px;
			left: 0;
		}
		&::before {
			position: absolute;
			content: "";
			width: 100%;
			height: 2px;
			background-color: #707070;
			bottom: -9px;
			left: 0;
		}
	}
	&__example-body {
		margin-right: 125px;
		display: flex;
		flex-direction: column;
		justify-content: center;

		@include mq() {
			margin-right: 0;
		}
	}

	&__example-title {
		font-size: 20px;
		font-weight: $bold;
		text-align: center;
	}
	&__example-text {
		font-size: 20px;
		font-weight: $bold;
		margin-top: 10px;
	}
	span {
		font-size: 14px;
		display: block;
		line-height: 1.428;
	}

	&__example-img {
		@include mq() {
			margin-top: 33px;
		}
		img {
		}
	}
}
////sec02/////
.p-u-scada02 {
	padding: 87px 0 126px;
	background-color: #97d3e2;
	margin-bottom: -250px;

	&__title {
		margin-top: 100px;
		padding-left: 80px;
		position: relative;
		font-size: 15px;

		@include mq() {
			margin-top: 50px;
			// padding-left: 20px;
		}

		&::before {
			position: absolute;
			content: "";
			background-color: $white;
			width: 6px;
			height: 34px;
			top: 50%;
			left: 58px;
			transform: translateY(-50%);
		}
	}
	&__imgs {
		ul {
			text-align: center;
			display: flex;
			margin-top: 54px;
			width: 950px;
			max-width: 100%;
			margin: 0 auto;

			@include mq(sm) {
				margin-top: 33.5px;
				flex-direction: column;
			}
			li {
				&:not(:first-child) {
					margin-left: 25px;

					@include mq(sm) {
						margin-left: 0;
						margin-top: 30px;
					}
				}
				img {
				}
				span {
					font-size: 14px;
					@include mq() {
						font-size: 12px;
					}
				}
			}
		}
		img {
		}
		span {
			margin-top: 23px;
			display: inline-block;
		}
	}
	&__imgs02 {
		margin-top: 193px;
		position: relative;
		z-index: 50;
		display: block;

		@include mq() {
			margin-top: 100px;
		}
	}
}

//
.mt-0 {
	margin-top: 0;
}

.top-40 {
	&::before {
		top: 40px;
	}
}

///////////////

.loopSlide {
	display: flex;
	margin: 0 calc(50% - 50vw);
	width: 100vw;
	margin-top: 105px;
	height: 100%;
	overflow: hidden;
	animation: 20s linear infinite sliderAnimation;
}

/*  */
.swiper-wrapper {
	transition-timing-function: linear;
}

.swiper {
	margin-top: 105px;

	@include mq() {
		margin-top: 50px;
	}
}

.loop-slider__img {
	position: relative;
	width: 100%;

	&::before {
		content: "";
		display: block;
		padding-top: 81.5%;
	}

	img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}
