@use "global" as *;

.c-img-padding--50 {
    position: relative;
    width: 100%;

    &::before {
        content: "";
        display: block;
        padding-top: 50%;
    }

    img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
    
    

    
