@use "global" as *;

// 
.c-btn {
    position: relative;
    width: 420px;
    height: 70px;
    display: block;
    border-radius: 12px;
    overflow: hidden;
    cursor: pointer;

    @include mq(sm){
        width: 290px;

    }

    a {
        img {
            position: absolute;
            top: 50%;
            left: 50%;
            -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
            width: 100%;
            display: block;
            -webkit-transition: 0.4s ease-in-out;
            transition: 0.4s ease-in-out;
        }
        &:hover img:nth-of-type(2) {
            opacity: 0;
        }
    }
}


.c-btn--service,
.c-btn--know-how{
    position: relative;
    width: 200px;
    height: 60px;
    margin: 0 auto;
    display: block;
    border-radius: 12px;
    overflow: hidden;
    cursor: pointer;
    transition: ease 0.6s;

    @include mq(){
        width: 130px;
    }

    @include mq(sm){
        width: 190px;

    }
    a {
        img {
            position: absolute;
            top: 50%;
            left: 50%;
            -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
            width: 100%;
            display: block;
            -webkit-transition: 0.4s ease-in-out;
            transition: 0.4s ease-in-out;
        }
        &:hover img:nth-of-type(2) {
            opacity: 0;
        }
    }
}


