@use "global" as *;
// 
.c-text-area {
    width: 712px;
    max-width: 100%;
    flex: 1;
    padding-left: 50px;

    @include mq() {
        margin-top: 30px;
        // padding-right: 20px;
        // padding-left: 20px;
        padding-left: 0;
    }
    h3 {
        font-size: 20px;
        font-weight: $bold;

        @include mq() {
            font-size: 17px;
            text-align: center;
        }
    }
    p {
        font-size: 16px;
        margin-top: 22px;
        font-family: $second-font-family;

        @include mq() {
            font-size: 14px;
            padding-top: 0;
        }
    }

    @include mq(sm) {
        margin-top: 44px;
    }
}
.c-text-area--border {
    h3 {
        font-size: 16px;
        font-weight: $bold;
        display: flex;
        align-items: center;
        @include mq() {
            font-size: 14px;
        }

        &::after {
            border-bottom: 3px dotted #969696;
            content: "";
            // position: absolute;
            // top: 50%;
            // left: 50%;
            // transform: translate(-50% ,-50%);
            width: 50%;
            margin-left: 10px
        }
    }
    p {
        font-size: 14px;
        margin-top: 27px;
        font-family: $second-font-family;

        @include mq() {
            font-size: 13px;
        }
    }
}

// 
.c-text-area--white {
    width: 712px;
    max-width: 100%;
    color: $white;
    flex: 1;
    @include mq(sm) {
        color: $black;
        margin-top: 36px;
        text-align: center;
    }
    h3 {
        font-size: 20px;
        font-weight: $bold;

        @include mq() {
            font-size: 17px;
            font-weight: normal;
        }
    }
    p {
        font-size: 14px;
        margin-top: 22px;
        font-family: $second-font-family;

        @include mq() {
            font-size: 13px;
        }
    }
}

//
.c-text-area--center {
    width: 900px;
    max-width: 100%;
    margin: 0 auto;
    padding-left: $padding-sp;
    padding-right: $padding-sp;

    @include mq() {
        padding-left: 0;
        padding-right: 0;
    }
    h3 {
        font-size: 20px;
        font-weight: $bold;
        text-align: center;

        @include mq() {
            font-size: 17px;
        }
    }
    p {
        font-size: 18px;
        margin-top: 60px;
        font-family: $second-font-family;

        @include mq() {
            font-size: 15px;
            margin-top: 23px;
        }
    }
}

///////
.tac {
    text-align: center;
}

//
.c-text-area--num {
    padding-left: 70px;
    position: relative;
    width: 730px;
    max-width: 100%;
    margin: 116px auto 0;

    @include mq() {
        margin: 30px auto 0;
    }
    @include mq(sm) {
        // padding-left: 0;
    }

    &__title {
        position: relative;
        padding-left: 45px;
        padding-right: 45px;
        font-size: 20px;
        font-weight: $bold;

        @include mq() {
            font-size: 16px;
            padding-left: 35px;
            padding-right: 0;
        }

        &:not(:first-child) {
            // padding:15px 40px 15px;
            padding-top: 35px;

            @include mq() {
                padding-top: 20px;
            }
        }

        &::before {
            content: attr(data-number);
            position: absolute;
            top: 24px;
            left: 0;
            padding-bottom: 5px;
            color: $letter;
            font-size: 45px;
            font-family: "DIN Condensed";
            font-weight: $bold;

            @include mq() {
                font-size: 34px;
                top: 10px;
            }
        }
    }
    .font-18 {
        font-size: 18px;
    }

    &__text {
        padding-left: 55px;
        // padding-top: 20px;
        font-size: 14px;

        @include mq() {
            padding-top: 20px;
            padding-left: 35px;
        }
    }

    h3 {
        font-size: 23px;
        font-weight: $bold;
        text-align: center;
        display: flex;
        justify-content: center;

        @include mq() {
            font-size: 13px;
        }

        &::before,
        &::after {
            content: "";
            width: 1px;
            height: 33px;
            background-color: $black;
        }

        &::before {
            margin-right: 30px;
            transform: rotate(-35deg);
        }

        &::after {
            margin-left: 30px;
            transform: rotate(35deg);
        }
    }
}
///title
.font-22 {
    font-size: 22px;
    font-weight: $bold;
}

.c-text-area--service {
    @include mq() {
        width: 100%;
        margin: 0 auto;
    }
    h3 {
        font-size: 30px;
        font-weight: $bold;
        position: relative;
        color: $letter;
        margin-top: 30px;

        @include mq() {
            font-size: 20px;
        }
    }
    &__sub-title {
        font-size: 20px;
        margin-top: 41px;
        font-weight: $bold;
        line-height: 1.4;
        @include mq() {
            font-size: 16px;
            margin-top: 20px;
        }
    }
    &__text {
        font-size: 14px;
        margin-top: 41px;
        line-height: 1.4;
        letter-spacing: 0.05em;
        font-family: $second-font-family;

        @include mq() {
            font-size: 14px;
            margin-top: 20px;
        }
    }

    ul {
        display: flex;
        margin-top: 36px;
        text-align: center;

        @include mq(sm) {
            justify-content: center;
        }

        @include mq() {
            margin-top: 30px;
        }
        li {
            font-weight: $bold;
            color: $letter;
            border: 1px solid $letter;
            padding: 17px 34px;
            border-radius: 12px;

            @include mq() {
                padding: 8px 15px;
                font-size: 13px;
            }

            &:not(:first-child) {
                margin-left: 12px;
            }
        }
    }
    span {
        display: inline-block;
        font-size: 16px;
        font-weight: $bold;
        color: #52b6cf;
        border-left: 1px solid #52b6cf;
        border-bottom: 1px solid #52b6cf;
        transform: rotate(-7deg);
        padding: 10px;
    }
}
