@use "global" as *;

.l-item {
    padding-top: 150px;

    @include mq() {
        padding-top: 108px;
    }
}

.p-item {
    &__inner {
        max-width: 980px;
        width: 100%;
        margin: 0 auto;
      
    }
    &__head-box {
        display: flex;
        justify-content: center;

        @include mq(sm) {
            flex-direction: column;
            align-items: center;
        }
    }

    &__btn-wrap {
        display: flex;
        justify-content: center;
        width:100%;
        max-width: 100%;
        margin: 75px auto 0;

        @include mq() {
            margin: 40px auto 0;
        }

        @include mq() {
            flex-wrap: wrap;
        }
    }
    &__explanation {
        background-color: $blue;
        padding: 100px 24px 46px;

        @include mq(){

            &:not(:first-child){
            margin-top: 30px;
            }
        }
    }
    &__btns {
        @include mq() {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        .item02,
        .item03 {
            margin-top: 75px;

            &:not(:first-child) {
                margin-top: 20px;
            }

            a {
                display: block;

                img {
                }
            }
        }
    }
}

///////////////////////////////////
//IoT
.grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px 18px;
    margin-top: 70px;
   

    @include mq() {
        justify-items: center;
        padding-left: 150px;
        padding-right: 150px;
    }
    @include mq(sm) {
     
        padding-left: 100px;
        padding-right: 100px;
    }
    @include mq(ssm) {
        justify-items: center;
        padding-left: 5px;
        padding-right: 5px;
    }
}

// 
.green {
    background-color: $green;
}

// 
.item01 {
    position: relative;
    width: 140px;
    height: 160px;
    // margin: 0 auto;
    display: block;
    border-radius: 12px;
    overflow: hidden;
    // cursor: pointer;

    @include mq(ssm) {
        width: 120px;
        height: 130px;
    }
    a {
        img {
            position: absolute;
            top: 50%;
            left: 50%;
            -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
            width: 100%;
            display: block;
            -webkit-transition: 0.4s ease-in-out;
            transition: 0.4s ease-in-out;
        }
        &:hover img:nth-of-type(2) {
            opacity: 0;
        }
    }
}
.item02,
.item03 {
    position: relative;
    width: 260px;
    height: 130px;
    // margin: 0 auto;
    display: block;
    border-radius: 12px;
    overflow: hidden;
    // cursor: pointer;
    a {
        img {
            position: absolute;
            top: 50%;
            left: 50%;
            -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
            width: 100%;
            display: block;
            -webkit-transition: 0.4s ease-in-out;
            transition: 0.4s ease-in-out;
        }
        &:hover img:nth-of-type(2) {
            opacity: 0;
        }
    }
}

// 
.p-modal-item {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
    display: none;

    &__content {
        width: 45vw;
        margin: 0 auto;
      


        @include mq() {
            width: 95vw;
        }
        @include mq(ssm) {
            width:100vw;
        }

        img {
            position: relative;
        }
    }
    &__icon {
        width: 30px;
        height: 30px;
        display: block;
        position: absolute;
        top: 4vw;
        right: 32px;

        @include mq(lg02){
            top: 5vw;
        }
        @include mq(){
            top: 10vw;
            width: 20px;
        }
    }

    // 
    &__bg {
        position: fixed;
        top: 0;
        left: 0;
        background-color: $black;
        opacity: 0.3;
        width: 100%;
        height: 100%;
        z-index: 100;
        display: none;
    }
}
