@use "global" as *;

.l-u-news {
	margin-bottom: -100px;
	padding-left: $padding-sp;
	padding-right: $padding-sp;
}

// CSS
.p-post-cards {
	width: 900px;
	max-width: 100%;
	margin: 0 auto;
	margin-top: 89px;
	display: flex;
	flex-wrap: wrap;
	// gap: 50px;
}

.p-post-card__news {
	width: calc(33.33% - 60px * 2 / 3);

	&:not(:nth-child(3n + 1)) {
		margin-left: 60px;
	}
	&:nth-child(n + 4) {
		margin-top: 100px;
	}

	@include mq(){
		width: calc(50% - 30px * 1 / 2);

		&:not(:nth-child(3n + 1)) {
			margin-left: 0px;
		}
		&:not(:nth-child(2n + 1)) {
			margin-left: 30px;
		}
		&:nth-child(n +3) {
			margin-top: 65px;
		}
	}
	@include mq(sm){
		width:100%;

		&:not(:nth-child(3n + 1)) {
			margin-left: 0px;
		}
		&:not(:nth-child(2n + 1)) {
			margin-left: 0px;
		}
		&:nth-child(n +2) {
			margin-top: 65px;
		}
	}
	
	
	&__img {
		position: relative;
		width: 100%;

		&::before {
			content: "";
			display: block;
			padding-top: 65%;
		}
		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	&__body {
	}
	&__wrap {
		display: flex;
		align-items: flex-end;
		padding: 14px 0;
		border-bottom: 1px solid #000;
	}
	&-date {
		margin-top: 12px;
		font-size: 13px;
	}

	&-title {
		font-size: 15px;
		padding-top: 10px;
	}

	&-btn {
		margin: 0 auto;
		margin-top: 25px;
	}
	&__pagenation {
		margin-top: 115px;
	}
}
