@use "global" as *;

.l-service {
	padding-top: 150px;
	// padding-bottom: 143px;
}

.p-service {
	&__inner {
		width: 970px;
		max-width: 100%;
		margin: 0 auto;
	}
	&__head-box {
		display: flex;
		justify-content: center;

		@include mq(sm) {
			flex-direction: column;
			align-items: center;
		}
	}
	&__support-wrap {
		display: flex;
		width: 1080px;
		max-width: 90%;
		margin: 84px auto 0;

		@include mq(sm) {
			flex-wrap: wrap;
			margin: 30px auto 0;
		}
	}
	&__support-item {
		font-family: $second-font-family;
		background-color: $blue;
		width: 50%;
		color: $white;
		padding: 70px 35px 30px;
		position: relative;

		@include mq() {
			width: 100%;
		}

		&:nth-child(2n) {
			background-color: $green;
		}

		&:not(:first-child) {
			margin-left: 30px;

			@include mq(sm) {
				margin-left: 0;
				margin-top: 30px;
			}
		}
		h3 {
			padding-left: 85px;
			font-size: 24px;
			letter-spacing: 0.05em;

			@include mq() {
				font-size: 17px;
			}
		}
		.sub-ttl {
			padding-left: 85px;
			font-size: 15px;
			display: block;
			margin-top: 5px;
			letter-spacing: 0.05em;

			@include mq() {
				font-size: 14px;
			}
		}
		p {
			margin-top: 30px;
			margin-bottom: 60px;
			font-size: 13px;
			letter-spacing: 0.05em;
			line-height: 1.77;

			@include mq() {
				font-size: 13px;
			}
		}
	}
	&__logo {
		width: 56px;
		position: absolute;
		top: 50px;
		left: 50px;

		@include mq(ssm) {
			top: 70px;
			left: 40px;
		}

		img {
		}
	}
	&__support-btn {
		&:hover {
			transform: translateX(15px);
		}
	}
	&__ {
	}
	&__ {
	}
}

.service-items {
}

.service-items {
	// margin-bottom: 10px;
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 10px;
	margin: 35px 0;
}
.service-item {
	font-size: 12px;
	border: 1px solid $white;
	// display: inline-block;
	padding: 6px 8px;
	border-radius: 10px;
	// margin-bottom: 10px;
	text-align: center;

	& + & {
		// margin-left: 8px;
		// margin-top: 12px;
	}
}
