@use "global" as *;

.c-sub-head {
    text-align: center;
    // padding-top: 215px;
    padding-top: 100px;
    // letter-spacing: 0.05em;

    @include mq() {
        padding-top: 68px;
    }

    &__logo {
        width: 50px;
        margin: 0 auto;
        img {
        }
    }
    &__title {
        margin-top: 27px;
        font-size: 27px;
        font-weight: $bold;

        @include mq() {
            font-size: 20px;
        }
    }

    p {
        display: inline-block;
        margin-top: 50px;
        border: 1px solid $black;
        border-radius: 7px;
        padding: 10px 36px;
        font-size: 14px;

        @include mq() {
            margin-top: 38px;
            font-size: 12px;
        }

        span {
            margin-left: 18px;
            font-size: 11px;
        }
    }
}
