@use "global" as *;

.l-news {
	padding-top: 88px;
	margin-bottom: -141px;
}
.p-news {
	&__inner {
		// display: flex;
		// justify-content: center;
		// width: 90%;
		// margin: 0 auto;
		// padding-left: 20px;
		// margin-bottom: 75px;

		// @include mq() {
		// 	padding-right: 20px;
		// }

		@include mq(sm) {
			// display: block;
			align-items: center;
		}
	}
	&__head-box {
		display: flex;
		justify-content: center;
		width: 90%;
		margin: 0 auto 130px;
		align-items: center;
		// padding-left: 100px;

		@include mq() {
			flex-direction: column;
			width: 100%;
			margin: 0 auto 80px;
			// padding-left: 0;
		}
	}
	&__items {
		width: 70%;
		height: 200px;
		overflow-y: scroll;
		-webkit-overflow-scrolling: touch;
		padding-right: 35px;
		// margin-bottom: 130px;

		@include mq() {
			margin-top: 52px;
			padding-right: 10px;
			width: 90%;
			margin: 0 auto;
			height: 150px;
		}
	}
	&__item {
		font-size: 15px;
		display: flex;
		// justify-content: center;
		letter-spacing: 0.24rem;
		margin-bottom: 35px;
		align-items: flex-end;
	

		@include mq(sm) {
			font-size: 13px;
			margin-bottom: 20px;
		}
	}
	&__date {
		width: 250px;
		text-align: center;
		border-bottom: 1px solid $black;
		margin-right: 25px;
		font-size: 15px;
		padding: 5px;


		@include mq() {
			width: 200px;
			font-size: 13px;
			margin-right: 10px;
		}
	}
	&__title {
		margin-right: rem(20);
		border-bottom: 1px solid $black;
		// width: calc(100% - 140px);
		width: 100%;
		// text-align: center;
		font-size: 15px;
		padding: 5px;

		@include mq(){
			font-size: 13px;
		}

	}
	&__more {
		font-size: 15px;
		display: inline-block;
		text-align: center;
		position: relative;
		border-bottom: 1px solid $black;
		width: 220px;

		@include mq(sm) {
			display: none;
		}
	}

	&__btn {
		margin: 0 auto;
		position: relative;
		z-index: 99;
	}

	////
	::-webkit-scrollbar {
		width: 7px;
		height: 7px;
	}
	::-webkit-scrollbar-track {
		background-color: $green;
	}
	::-webkit-scrollbar-thumb {
		background: $blue;
		width: 130px;
	}
}
