.p-top-header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    height: 100px;
    background-color: transparent;

    @include mq() {
        height: 70px;
    }

    // 
    // &::before {
    //     content: "";
    //     position: absolute;
    //     background-image: url(../images/common/header-bg02.png);
    //     background-size: cover;
    //     background-repeat: no-repeat;
    //     width: 590px;
    //     height: 100%;
    //     bottom: 0;
    //     left: 0px;
    //     z-index: -1;

    //     @include mq(lg02) {
    //         background-image: url(../images/common/mv-bg-sp.png);
    //         width: 249.22px;
    //     }
    // }

    &__inner {
        width: 100%;
        height: inherit;
        line-height: 100px;
        display: flex;
        justify-content: space-between;
    }
    &__left {
        display: flex;
        width: 40%;
        flex-wrap: wrap;
    }
    &__logo {
        width: rem(160);
        height: 100%;
        position: relative;
        z-index: 200;

        @include mq(lg02) {
            width: 109px;
        }

        a {
        }
        img {
        }
    }
    &__tel {
        color: $white;
        z-index: 200;
        position: relative;

        @include mq(lg02) {
            display: none;
        }
        span {
            font-size: 11px;
            margin-right: 20px;
            margin-left: 30px;
        }
        a {
            font-size: 16px;
            font-weight: $bold;
        }
    }

    &__tel.is-change {
        color: $letter;
    }


    &__nav {
        @include mq(lg02) {
            display: none;
        }
    }
    &__nav-items {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-end;
    }
    &__nav-item {
        font-size: 13px;
        padding-left: 10px;
        padding-right: 10px;
        position: relative;
        color: #003865;

        &:nth-child(7) {
            margin-right: 20px;
        }

        &::before {
            position: absolute;
            content: "";
            background-color: #0c68ae;
            width: 1px;
            height: 20px;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
        }
    }

    a {
        // display: block;
        // padding-top: 20px;
        // padding-bottom: 20px;
    }
    .logo-facebook {
        padding: 0 13px;
        display: flex;
        align-items: center;
        width: 45px;
        margin-right: 20px;
    }

    &__contact-btn {
        width: rem(100);
        height: rem(100);
        background-color: #192983;
        color: $white;
        font-size: 12px;
        text-align: center;
        position: relative;
        padding-top: 20px;
        transition: ease 0.6s;

        a {
            display: block;
        }

        &::before {
            position: absolute;
            content: "";
            background-image: url(../images/common/logo-mail.png);
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            width: 30px;
            height: 22px;
            top: 30%;
            left: 50%;
            transform: translateX(-50%);
        }

        &:hover {
            opacity: 0.8;
        }
    }
}

.p-header-nav {
    ul {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        font-size: 12px;
    }

    // 
    &__item {
        padding: 0 10px;
        display: block;
        transition: ease 0.8s;
        // position: relative;
        // left: 100px;

        &:not(:last-child) {
            border-right: 1px solid $black;
        }

        &:hover {
            // color: $base;
        }

        a {
            padding: 40px 0;
            letter-spacing: 0.1em;
        }
    }

    // 
    // 
    &__child {
        background-color: $white;
        color: $letter;
        border: 1px solid #b3b3b3;
        width: 40vw;
        position: absolute;
        top: 90px;
        left: -16vw;
        z-index: 200;
        font-size: 15px;
        display: none;
        ul {
            display: flex;
            flex-direction: column;
            align-items: stretch;
            transition: all 0.6s;
        }
    }

    &__child-title {
        p {
            font-size: 24px;
            padding-left: rem(60);
        }
    }

    &__child-items {
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        a {
            padding: 0;

            &:hover {
                color: #0093b8;
            }
        }
    }

    // 
    &__child-service-item01,
    &__child-service-item02,
    &__child-service-item03 {
        width: 33.333%;
        text-align: center;
        position: relative;

        &:not(:last-child) {
            &::before {
                position: absolute;
                content: "";
                width: 1px;
                height: 30px;
                background-color: #0068ae;
                top: 50%;
                left: 100%;
                transform: translate(-50%, -50%);
            }
        }

        a {
        }
    }

    &__child-service-item01 {
        &::before {
            content: "";
            display: inline-block;
            background-image: url(../images/common/service-icon01.png);
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
            width: 25px;
            height: 25px;
        }
    }
    &__child-service-item02 {
        &::before {
            content: "";
            display: inline-block;
            background-image: url(../images/common/service-icon02.png);
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
            width: 25px;
            height: 25px;
        }
    }
    &__child-service-item03 {
        // &::before {
        //     content: "";
        //     display: inline-block;
        //     background-image: url(../images/common/service-icon03.png);
        //     background-position: center;
        //     background-size: cover;
        //     background-repeat: no-repeat;
        //     width: 25px;
        //     height: 25px;
        // }
    }

    // 
    &__child-sensible-item01,
    &__child-sensible-item02 {
        width: 50%;
        text-align: center;

        &:not(:last-child) {
            &::before {
                position: absolute;
                content: "";
                width: 1px;
                height: 30px;
                background-color: #0068ae;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }

        a {
        }
    }
}

// 
.p-top-header__nav-item:hover .p-header-nav__child {
    display: block;
}
