/////////////////////////
.p-header-drawer__icon {
    position: fixed;
    top: 40px;
    right: 15px;
    z-index: 1001;
    transition: transform 0.5s ease 0s;
    display: none;

    @include mq(lg02) {
        display: block;
    }
    @include mq(ssm) {
        top: 20px;
    }

    // 
    &.is_active {
        .icon__bar1 {
            transform: rotate(-45deg);
            top: rem(8);
            background-color: #fff;
            transition: transform 0.5s ease 0s;
        }
        .icon__bar2 {
            display: none;
        }

        .icon__bar3 {
            transform: rotate(45deg);
            top: rem(8);
            background-color: #fff;
            transition: transform 0.5s ease 0s;
        }
    }
}

// TELmail
.p-header-drawer__contact-icon {
    display: none;
    @include mq(lg02) {
        display: block;
    }
}
.p-header-drawer__icon-tel {
    position: absolute;
    width: 20px;
    top: 38px;
    right: 60px;

    @include mq(ssm) {
        top: 20px;
    }
}
.p-header-drawer__icon-mail {
    position: absolute;
    width: 20px;
    top: 40px;
    right: 100px;

    @include mq(ssm) {
        top: 22px;
    }
}

// 
.p-heder-drawer_icon-bars {
    width: 28px;
    height: 12px;
    display: block;
    position: relative;
}
.icon__bar1,
.icon__bar2,
.icon__bar3 {
    position: absolute;
    display: block;
    width: 28px;
    height: 2px;
    background: #0c68ae;
    top: 0;
    left: 0;
}
.icon__bar1 {
    top: 0;
}
.icon__bar2 {
    top: 6px;
}
.icon__bar3 {
    top: 12px;
}

//

.p-header-drawer__content {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    right: 0%;
    background-color: #0068ae;
    // z-index: 500;
    transform: translateX(105%);
    transition: transform 0.5s ease 0s;
    padding: 0px 40px 30px;
    display: none;
    overflow: scroll;

    @include mq(lg02) {
        display: block;
        width: 30%;
    }
    @include mq() {
        width: 50%;
    }
    @include mq(sm) {
        width: 100%;
    }
    &.is_active {
        transform: translateX(0);
    }

    .p-header-drawer__logo-wrap {
        display: flex;
        align-items: center;
        margin-bottom: 30px;

        .p-top-header__logo {
            width: 50px;
        }
        .p-top-header__company-name {
            font-family: $second-font-family;
            letter-spacing: 0.08em;

            p {
                color: $white;
                font-size: 20px;
            }
            span {
                font-size: 16px;
            }
        }
    }
}

.p-header-drawer__content_items {
    color: rgba(255, 255, 255, 0.8);
    font-size: 14px;
    font-weight: $normal;
    padding-top: 120px;
}
.drawer__content_item {
    padding-bottom: 5px;
